import React from 'react';
import Style from '../css/Intro.module.css';
import { ReactTyped } from 'react-typed';

const Intro = () => {
    return (
        <section id="intro" className={Style.section}>
            <ReactTyped
                className={Style.title}
                strings={[
                    "  <br>  :)",
                ]}
                typeSpeed={70}
                backSpeed={0}
                startDelay={1000}
            />
        </section>
    );
};

export default Intro;